<script>
  import CONFIG from "../../config";

  export default {
    name: "make-patrol-record",
    data() {
      return {
        checkAllStatus: false,
        maintRecord: [],
        maintStaffMap: {},
        keepuped: false,  //  是否已经维修过
        loading: false,
        imageUrl: [],
        action: CONFIG.baseUrl + "/maint/uploadFile",
        header: {
          token: uni.getStorageSync("token"),
        },
        formData: {
          fileKey: "keepup-record",
        },
        fileList: [],
        valueResult: "",
        resultList: [
          {
            title: "未完成",
            name: "0"
          },
          {
            title: "维修完成",
            name: "1"
          }
        ],
        resultMap: {
          "0": "未完成",
          "1": "维修完成"
        },
        form: {
          taskId: "",
          sendId: "",
          statusFlag: "",
          remarks: "",
          maintPic: ""
        },
        normalOrNotList: [
          {
            title: "正常",
            name: "0"
          },
          {
            title: "异常",
            name: "1"
          }
        ],
        normalOrNotMap: {
          '0': '正常',
          '1': '异常'
        },
        background: {
          'background': '#f2f2f8'
        }
      }
    },
    computed: {
    },
    watch: {
    },
    onLoad(options) {
      this.form.taskId = options.taskId;
      this.form.sendId = options.sendId;
      // const user = uni.getStorageSync("user");
      // if (user) {
      //   this.form.patrolStaffSign = user.staffName;
      // }
      // const now = new Date().getTime();
      // this.form.patrolTime = this.$u.timeFormat(now, 'yyyy-mm-dd hh:MM:ss')
    },
    onShow() {
      this.getMaintList();//  获取养护记录
    },
    onReady() {
    },
    methods: {
      resultChange() {
      },
      back() {
        uni.navigateBack({
          delta: 1
        })
      },
      onSuccess(data, index, lists, name) {
        if (data.code === 200 && data.data) {
          this.imageUrl.push(data.data);
        }
      },
      onRemove(index, lists, name) {
        this.imageUrl.splice(index, 1);
      },
      //  获取养护的表单
      getMaintList() {
        this.$u
            .post('/maint/getWeihuList', {
              taskId: this.form.taskId
            }).then(res => {
          if (res.code === 200 && res.data) {
            this.keepuped = true;
          } else {
            this.keepuped = false;
          }
        })
      },
      submitForm() {
        if (this.imageUrl.length) {
          this.form.maintPic = this.imageUrl.join(',');
        }
        if (this.form.statusFlag === '') {
          this.$refs.uToast.show({
            title: '请选择试运情况',
            type: 'warning'
          });
          return
        }

        // const now = new Date().getTime();
        // this.form.patrolTime = this.$u.timeFormat(now, 'yyyy-mm-dd hh:MM:ss')

        const finalForm = Object.assign({}, this.form);

        console.log(finalForm);
        this.loading = true;
        this.$u
            .post('/maint/saveWeihuRecord', finalForm)
            .then(res => {
              console.log(res);
              if (res.code === 200) {
                this.$refs.uToast.show({
                  title: res.message,
                  type: 'success'
                });
                setTimeout(() => {
                  uni.navigateTo({
                    url: '/pages/home/index'
                  })
                }, 300)
              }
            }).catch(e => {
        }).finally(_ => {
          this.loading = false;
        })
      },
      //  图片预览
      doPreviewImage(images, index) {
        uni.previewImage({
          urls: images,
          current: index,
          success: () => {
          },
          fail: () => {
            uni.showToast({
              title: '预览图片失败',
              icon: 'none'
            });
          }
        });
      },
      toggleCheckAll() {
        this.checkAllStatus = !this.checkAllStatus;
      },
      statusChange(v) {

      }
    }
  }
</script>

